body {
  margin: 0;
  padding: 0;
  background: whitesmoke;
  color: rgb(51, 50, 50);
  font-family: 'Crimson Pro', serif;
}

#root {
  margin: 0 24px;
  position: relative;
  min-height: 100vh;
}
.TodoCounter {
	font-size: 24px;
	text-align: center;
	margin: 0;
	padding: 48px;
}
.TodoSearch {
	background: white;
	border-radius: 2px;
	border: 2px solid rgb(66, 65, 65);
	margin: 0 24px;
	height: 64px;
	width: calc(100% - 62px);
	font-size: 24px;
	text-align: center;
	font-family: 'Courier New', Courier, monospace;
	font-weight: 400;
	color: rgb(24, 23, 23);
	box-shadow: 0px 5px 50px rgb(36, 28, 28);
}

.TodoSearch::-webkit-input-placeholder {
	color:aliceblue;
	font-family:'Courier New', Courier, monospace;
	font-weight: 400;	
}

.TodoSearch:-ms-input-placeholder {
	color:aliceblue;
	font-family:'Courier New', Courier, monospace;
	font-weight: 400;	
}

.TodoSearch::placeholder {
	color:aliceblue;
	font-family:'Courier New', Courier, monospace;
	font-weight: 400;	
}

.TodoSearch:focus {
	outline-color: aquamarine;
}
ul {
	margin: 0;
	padding: 0 0 56px 0;
	list-style: none;
}
.Icon-container {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 48px;
  width: 48px;
  /* background-color: #CCC; */
}
.Icon-container--check {
  position: absolute;
  left: 12px;
}
.Icon-container--delete {
  position: absolute;
  top: -24px;
  right: 0;
}

.Icon-svg {
  width: 24px;
  height: 24px;
  transition: fill .15s;
}
.Icon-container:hover .Icon-svg--check {
  fill: #4caf50;
}
.Icon-container:hover .Icon-svg--delete {
  fill: red;
}
.TodoItem {
  background-color: #FAFAFA;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
}

.TodoItem-p {
  margin: 24px 0 24px 24px;
  width: calc(100% - 120px);
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
}
.TodoItem-p--complete {
  text-decoration: line-through;
}

.Icon {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 48px;
  width: 48px;
  font-size: 24px;
  font-weight: bold;
  /* background-color: #CCC; */
}

.Icon-check {
  position: absolute;
  left: 12px;
}
.Icon-check--active {
  color: #4caf50;
}

.Icon-delete {
  position: absolute;
  top: -24px;
  right: 0;
}
.Icon-delete:hover {
  color: red;
}
form {
  width: 90%;
  max-width: 300px;
  background-color: #fff;
  padding: 33px 40px;
  display: flex;
  align-content: center;
  flex-direction: column;
}

label {
  text-align: center;
  font-weight: bold;
  font-size: #1E1E1F;
  margin-bottom: 26px;
}

textarea {
  background-color: #F9FBFC;
  border: 2px, solid #202329;
  border-radius: 2px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  color: #1E1E1F;
  font-size: 20px;
  text-align: center;
  padding: 12px;
  height: 96px;
  width: calc(100%-25px);
}

textarea::-webkit-input-placeholder {
color: #a5a5a5;
font-family: 'Montserrat';
font-weight: 400;
}

textarea:-ms-input-placeholder {
color: #a5a5a5;
font-family: 'Montserrat';
font-weight: 400;
}

textarea::placeholder {
color: #a5a5a5;
font-family: 'Montserrat';
font-weight: 400;
}

textarea:focus {
  outline-color: #61DAFA;
}

.TodoForm-buttonContainer {
  margin-top: 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.TodoForm-button {
  cursor: pointer;
  display: inline-block;
  font-size: 20px;
  color: #202329;
  font-weight: 400;
  width: 120px;
  height: 48px;
  border-radius: 2px;
  border: none;
  font-family: 'Montserrat';
}

.TodoForm-button--add {
  background-color: #61DAFA;
  box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
}

.TodoForm-button--cancel {
  background: transparent;
}
.CreateTodoButton {
	background-color: aqua;
	box-shadow: 0px 5px 25px grey;
	border: none;
	border-radius: 50%;
	cursor: pointer;
	font-size: 50px;
	position: fixed;
	bottom: 24px;
	right: 24px;
	font-weight: bold;
	color: white;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 64px;
	width: 64px;

	-webkit-transform: rotate(0);

	        transform: rotate(0);
	transition: .3s ease;
	z-index: 1;
}

.CreateTodoButtonhover {
	-webkit-transform: rotate(224deg);
	        transform: rotate(224deg);
}
.ModalBackground {
  background: rgba(32,35,41,.8);
  position: fixed;
  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}
.LoadingTodo-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
}

.LoadingTodo-text {
  margin: 24px 0 24px 24px;
  width: calc(100% - 120px);
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
}

.LoadingTodo-completeIcon,
.LoadingTodo-deleteIcon {
	border-radius: 50px;
	height: 48px;
	width: 48px;
}

.LoadingTodo-completeIcon {
	position: absolute;
	left: 12px;
}

.LoadingTodo-deleteIcon {
	position: absolute;
	top: -24px;
	right: 0;
}


.LoadingTodo-container,
.LoadingTodo-completeIcon,
.LoadingTodo-deleteIcon {
  background: linear-gradient(90deg, rgba(250,250,250,1), rgb(200, 199, 199));
  background-size: 400% 400%;
  -webkit-animation: loadingAnimation 3s ease-in-out infinite;
          animation: loadingAnimation 3s ease-in-out infinite;
}

@-webkit-keyframes loadingAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes loadingAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
